<template>
    <div class="goodsList-warpper" >
        <el-row>
            <p>
               <span>{{curCity}}美团</span>
               <span>></span>
               <span>{{curCity}}{{$route.params.name}}</span>
            </p>
        </el-row>
        <el-row>
            <el-col :span="18">
                <div class="select-goods-nav">
                    <IsSelected/>
                    <SelectGoodsNav :selectName="item.selectName" :selectGoodsList="item.selectArr" :curIndex="index"
                       v-for="(item,index) in myList" :key="index"/>
                   
                </div>
                 <div class="select-goods-nav">
                     <EveryGoods/>
                 </div>
            </el-col>
            <el-col :span="6" id="fixed-box">
                <div class="right-side-box">
                    <RightSideBox @moveing="watchMoving"/>
                     <div :class="{'Normal-occupancy':true,'move-occupancy':isMoving}"></div>
                    <RightSideGoodsList/>
                </div>
                <div class="right-side-goodsList">
                    <EveryGoodsItem/>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="18">
                 <div class="goodsList-pagination">
                <el-pagination
              background
               layout="prev, pager, next"
               :total="goodsList.length"
               :page-size="5"
               @current-change="changePage">
                </el-pagination>
            </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import {mapState,mapMutations} from 'vuex'
import SelectGoodsNav from '@/components/RouterView/goodsList/SelectGoodsNav.vue'
import IsSelected from '@/components/RouterView/goodsList/IsSelected.vue'
import EveryGoods from '@/components/RouterView/goodsList/EveryGoods.vue'
import RightSideBox from '@/components/RouterView/goodsList/RightSideBox.vue'
import EveryGoodsItem from '@/components/RouterView/goodsList/EveryGoodsItem.vue'
import RightSideGoodsList from '@/components/RouterView/goodsList/RightSideGoodsList.vue'
export default {
    components:{
        SelectGoodsNav,
        IsSelected,
        EveryGoods,
        RightSideBox,
        EveryGoodsItem,
        RightSideGoodsList
    },
    methods:{
        changePage(curPage){
           this.changeCurPage(curPage);
           console.log(this.curPageIndex);
           document.documentElement.scrollTop = 0;
        },
        watchMoving(boolean){
             this.isMoving = boolean;
        },
        ...mapMutations(['changeCurPage'])
    },
    data(){
        return {
            isMoving:false,
            myList:[
                {
                    selectName:'分类',
                    selectArr:[
                    {
                    name:'美食',
                    arr:['全部','日本菜','饮品店','面包甜点',
                         '生日蛋糕','火锅','自助餐','小吃快餐']
                     },
                       {
                    name:'酒店住宿',
                    arr:['全部','经济型酒店','高端酒店','高星酒店','低星酒店',
                         '公寓','民宿','多人出行']
                     },
                     {
                         name:'周边游',
                         arr:['全部','景点','酒景套餐']
                     },
                     {
                         name:'休闲娱乐',
                         arr:['全部','足疗/按摩','网吧网咖']
                     },
                     {
                         name:'生活服务',
                         arr:['全部','照片冲印','鲜花','房屋地产']
                     },
                     {
                         name:"时尚购",
                         arr:['全部','鲜花']
                     },
                     {
                         name:"旅游",
                         arr:['全部','国内游']
                     },
                     {
                         name:"丽人",
                         arr:['全部','美发美容','美体','美甲美睫','化妆品']
                     }
                              ],
                },
                {
                    selectName:'区域',
                    selectArr:[
                               {
                       name:'推荐商圈',
                       arr:['台东','万达广场','万达CBD','家佳源佳世客','马濠公园','宏程新天地','香港中路沿线',
                           ' 浮山后']
                                },
                                {
                                    name:"市北区",
                                    arr:['全部','台东步行街','科技街','小村庄','海琴广场','浮山后','海云庵','万达CBD',
                                         '四方利群']
                                },
                                
                             ]
                }
               
            ]
        }
    },
   computed:{
       ...mapState(['curCity','goodsList'])
   }
}
</script>

<style lang="less">
  @import url('../../../assets/goodsList/goodsList.less');
</style>



