<template>
  
       
            <div class="is-selected"
              v-if="isSelectedArr.some((ele)=>{
                  return ele !== undefined && ele !== '' && ele
              })">
            
         <el-row>
            <el-col :span="4">
                <span>已选条件</span>
            </el-col>
            <el-col :span="20">
                  <div class="selected-arr">
                     <template v-for="(item,index) in isSelectedArr">
                          <span class="selected-arr-item"  :key="item"
                           @click.stop="deleteSelected(index)" v-if="item && item != ''">
                         
                              {{item}}
                       
                      </span>
                     </template>
                      <span class="delete" @click.stop="changeSelected">· 清除全部</span>
                  </div>
            </el-col>
        </el-row>
        
         
    </div>
    
 
</template>

<script>
import {mapState,mapMutations} from 'vuex'
export default {
  computed:{
      ...mapState(['isSelectedArr'])
  },
  methods:{
      changeSelected(){
         this.isSelectedPush({index:-1});
         
      },
      deleteSelected(index){
         this.isSelectedPush({index})
      },
      ...mapMutations(['isSelectedPush'])
  }
}
</script>

<style>

</style>



