<template>
    <div :class="{'right-side-map':true,'scroll-fixed':mapFixed}">
        <TxMap/>
    </div>
</template>

<script>
import TxMap from '@/components/RouterView/goodsList/TxMap.vue'
export default {
    data(){
        return {
            mapFixed:false,
            domTop:0
        }
    },
   components:{
       TxMap
   },
   methods:{
        watchScroll(){
         const documentTop = document.documentElement.scrollTop;
         const map = document.querySelector('#myMap');
         const mapBoxTop = document.querySelector('.right-side-box').getBoundingClientRect().top;
         const mapTop = map.getBoundingClientRect().top;
  
           if(mapTop <= 0){
               this.mapFixed = true;
               this.$emit('moveing',true)
           }
           if(mapBoxTop + 10 >= 0){
               this.mapFixed = false;
                 this.$emit('moveing',false)
           }
      },
   },
   mounted(){
       
       window.addEventListener('scroll',()=>{
          this.watchScroll();
       });
   }
}
</script>

<style lang="less">
  @import url('../../../assets/goodsList/rightside.less');
</style>



