<template>
    <div :class="{'goods-list-item':true,'right-side-goodsList-item':boxNewClass}"  v-if="itemIndex + 1 >= curPageIndex * 5 - 4 && itemIndex + 1 <= curPageIndex * 5 || itemIndex == undefined">
        <a href="#">
            <img :src="goodsListObj.imgUrl" alt="goods-list">
            <span v-if="itemIndex >= 0">{{itemIndex + 1}}</span>
        </a>
        <div :class="{'right-side-content-box':contentNewClass}">
            <p class="big-title">{{goodsListObj.bigTitle}}</p>
            <p class="score-title" :style="{'display':scoreDisPlay}">
                <span v-if="goodsListObj.score !== '暂无评' && !goodsListObj.score">{{goodsListObj.score}}</span>
                <el-rate
                v-else
                :value="Number(value)"
                disabled
                text-color="#ff9900"
               >
                </el-rate>
                <span v-if="goodsListObj.score && goodsListObj.score >= 4 && !goodsListObj.scoreNum">很好</span>
                <span v-if="goodsListObj.score && goodsListObj.score >= 2 && goodsListObj.score < 4 && !goodsListObj.scoreNum">一般</span>
                <span v-if="goodsListObj.score < 2 && !goodsListObj.scoreNum">较差</span>
                <span v-if="!goodsListObj.scoreNum">{{goodsListObj.score}}分</span>
                <span v-if="goodsListObj.scorePeople">{{goodsListObj.scorePeople}}人评论</span>
                <span v-if="goodsListObj.scoreNum" class="score-num">{{goodsListObj.scoreNum}}个评价</span>
            </p>
            <p class="type-address-title" v-if="goodsListObj.address || goodsListObj.city ">
                <span v-if="goodsListObj.type">{{goodsListObj.type}}|{{goodsListObj.city}}</span>
                <span v-if="goodsListObj.address">{{goodsListObj.address}}</span>
                <span v-if="goodsListObj.city && !goodsListObj.address">{{goodsListObj.city}}</span>
            </p>
            <p class="price-title">
                <span v-if="goodsListObj.price !== '暂无价格' && goodsListObj.price !== 0" class="goodsList-price">￥{{goodsListObj.price}}元起</span>
                <span v-if="goodsListObj.price == 0" class="goodsList-price">暂无价格</span>
                <span v-if="goodsListObj.price == '暂无价格'" class="goodsList-price">{{goodsListObj.price}}</span>
            </p>
        </div>
    </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
  props:['goodsListObj','itemIndex','boxNewClass','scoreDisPlay','contentNewClass'],
  data(){
      return {
          value:this.goodsListObj.score
      }
  },
  computed:{
      ...mapState(['curPageIndex'])
  }
}
</script>

<style lang="less">
  @import url('../../../assets/everygoods/everygoodsitem.less');
</style>



