<template>
    <el-row class="select-goods-components" align="top" type="flex">
         <el-col :span="3" class="select-title-box">
             <span class="select-goods-name">{{selectName}}</span>
         </el-col>
         <el-col :span="3" class="select-title-box">
             <span :class="{'tag':itemActive == '' || isSelectedArr[curIndex] == undefined || isSelectedArr[curIndex] == '',
                  'all-choose':true}"
                   @click.stop="changeSelected">全部</span>
         </el-col>
         <el-col :span="18" class="select-box" @mouseleave.native="myMouseLeave">
              
                  <SelectGoodsNavItem v-for="(item,index) in selectGoodsList" :key="index"
                                      :selectItemName="item.name" :selectItemList="item.arr"
                                      @isActive="changeItemActive" :active="itemActive"
                                     :curIndex="curIndex">
                                     
                                      
                                     
                                     </SelectGoodsNavItem>
              
         </el-col>
    </el-row>
</template>

<script>
import SelectGoodsNavItem from '@/components/RouterView/goodsList/SelectGoodsNavItem.vue'
import {mapState,mapMutations} from 'vuex'
export default {
   props:['selectName','selectGoodsList','curIndex'],
   components:{
       SelectGoodsNavItem
   },
   data(){
       return {
           itemActive:'',
           elseInit:''
       }
   },
   computed:{
      ...mapState(['isSelectedArr'])
   },
   methods:{
       changeItemActive(name){
          this.itemActive = name;
       },
       changeSelected(){
           this.isSelectedPush({index:this.curIndex});
           
       },
       myMouseLeave(){
           console.log('1231')
           this.itemActive = null;
       },
       ...mapMutations(['isSelectedPush'])
       
   }
}
</script>

<style lang="less">
 @import url('../../../assets/goodsList/selectgoodscomponents.less');
</style>



