<template>
    <div class="sort-option">
        <div v-for="(item,index) in sortArr" :key="index"
              @click.prevent="changeActive(index)" :class="{
                                                     }">
            <span :class="{'price-sort':item == '价格排序',
                           'add-active':item == '价格排序' && sortActiveArr[index] == 1,
                           'click-active':sortActiveArr[index] || index == oldIndex,
                           'minus-active':item == '价格排序' && sortActiveArr[index] == 2}"
                           >
                {{item == '价格排序' ? sortActiveArr[index] == 1 ? '价格最低' : sortActiveArr[index] == 2 ? '价格最高' : item : item}}
            </span>
        </div>
    </div>
</template>

<script>
import {mapMutations} from 'vuex'

export default {
   data(){
       return {
           sortArr:['智能排序','价格排序','人气最高','评价最高'],
           sortActiveArr:[],
           oldIndex:0,
           curStr:''
       }
   },
   methods:{
       changeActive(index){
            
            if(this.sortActiveArr.length == 0){
                
                this.sortActiveArr[this.sortArr.length - 1] = false;
                if(this.sortArr[index] == '价格排序'){
                    this.sortActiveArr[index] = 0;
                }
               
            }
            
            if(this.sortArr[index] == '价格排序'){
                let num = this.sortActiveArr[index];
                this.sortActiveArr.splice(index,1,++num);
                
                this.curStr = num == 1 ? '价格最低' : num == 2 ? '价格最高' : num == 3 ? '价格最低' : '';
                //num为3时会被重置为1,则也为价格最低
               
                if(num == 3){
                    this.sortActiveArr[index] = 1;
                    this.sortActiveArr.splice(index,1,1);
                }
                
            }else{
               this.sortActiveArr[index] = true;
               this.sortActiveArr.splice(index,1,true);
               this.curStr = this.sortArr[index];
            
             
            }
              for(let i = 0;i < this.sortActiveArr.length;i++){
                    if(i !== index && this.sortArr[i] !== '价格排序'){
                        this.sortActiveArr.splice(i,1,false)
                    }else if(i !== index && this.sortArr[i] == '价格排序'){
                        this.sortActiveArr.splice(i,1,0);
                    }
               }
            this.oldIndex = index;
             this.reorderGoodsList(this.curStr);
           
            
       },
       ...mapMutations(['reorderGoodsList'])
   }
}
</script>

<style lang="less">
   @import url('../../../assets/everygoods/everygoodsnav.less');
</style>



