<template>
    <div id="myMap">
    
    </div>
</template>

<script>
export default {
    
    methods:{
       createMap(){
               
               
    var myLatlng = new qq.maps.LatLng(39.916527,116.397128);
    var myOptions = {
    zoom: 13,
    center: myLatlng,
                       };
   var map = new qq.maps.Map(document.getElementById("myMap"), myOptions); 
               map.panTo(new qq.maps.LatLng(39.916527,116.397128));
               var marker = new qq.maps.Marker({
                   position: myLatlng,
                   map: map
               });
         
       },
     
    
    },
    mounted(){
       this.createMap();

      
    }
}
</script>

<style>
  #myMap{
        height: 220px;
    width: 230px;
    border-radius: 4px;
    margin-bottom: 10px;
    border: 1px solid #e5e5e5;
  }
</style>



