<template>
    <div>
        <EveryGoodsNav/>
       <div class="goods-list-item-box">
            <EveryGoodsItem v-for="(item,index) in goodsList" :key="index" :goodsListObj="item" :itemIndex="index"/>
       </div>
    </div>
</template>

<script>
import EveryGoodsNav from '@/components/RouterView/goodsList/EveryGoodsNav.vue'
import EveryGoodsItem from '@/components/RouterView/goodsList/EveryGoodsItem.vue'
import {mapState} from 'vuex'
export default {
   components:{
       EveryGoodsNav,
       EveryGoodsItem
   },
   computed:{
       ...mapState(['goodsList'])
   },
}
</script>

<style lang="less">
  @import url('../../../assets/everygoods/everygoods.less');
</style>



