<template>
    <div class="select-goods-item">
        <div :class="{'select-name-active':(this.mySelectItemName !== this.selectItemName || isAll) 
                                            && isSelectedArr[curIndex] === this.mySelectItemName}"
             @mouseenter.stop="changeActive(selectItemName)">
             
                 <span >{{mySelectItemName}}</span>
                 <i class="el-icon-caret-bottom"></i>
            
        </div>
         <div class="select-goods-active" v-show="myActive === this.selectItemName && isSelectBlock" 
              >
                        <p >{{selectItemName}}</p>
                         <div v-for="(item,index) in Math.ceil(mySelectItemList.length / 4)" :key="index">
                             <template v-for="(tem,dex) in 4">
                                 <span class="select-goods-active-item"  :key="dex"
                                  @click.stop="chooseGoods(index,dex)" v-if="mySelectItemList.length < 4 ? dex < 3 : true">
                                 <span :class="{'select-goods-font':true,'select-active':(isChoose[index*4 + dex] && isSelectedArr[curIndex] === mySelectItemList[index*4 + dex]
                                           ) || (index == 0 && dex == 0 && isSelectedArr[curIndex] == selectItemName)
                                                    }"
                                         >
                                     {{selectItemList[index*4 + dex]}}
                                 </span>
                             </span>
                             </template>
                         </div>
                    </div>
    </div>
</template>

<script>
import {mapMutations,mapState} from 'vuex'
export default {
    props:['selectItemName','selectItemList','active','curIndex'],
    data(){
        return {
            isSelectBlock:false,
            isChoose:[],
            mySelectItemName:this.selectItemName,
            isAll:false
           
        }
    },
    computed:{
       mySelectItemList(){
           return this.selectItemList;
       },
       myActive(){
           if(this.isSelectedArr[this.curIndex] !== this.mySelectItemName){
               this.mySelectItemName = this.selectItemName;
           }
           return this.active;
       },
       ...mapState(['isSelectedArr'])
    },
    created(){
       
        
    },
    methods:{
        changeActive(name){
            this.isSelectBlock = true;
            this.$emit('isActive',name);
        },
        chooseGoods(index,dex){
            if(this.isChoose[index*4 + dex]){
               
                console.log('已经存在')
                console.log(this.isChoose)
                this.isChoose.splice(0);
                this.mySelectItemName = this.selectItemName;
                this.isSelectedPush({index:this.curIndex})
                this.isAll = false;
                
                
                
            }else{
               console.log('没有存在')
               this.isChoose.splice(0);
                this.isChoose[index*4 + dex] = true;
                if(index == 0 && dex == 0){
                     this.mySelectItemName = this.selectItemName;
                     this.isAll = true;
                }else{
                    this.mySelectItemName = this.mySelectItemList[index*4 + dex];
                    this.isAll = false;
                }
                this.isSelectedPush({value:this.mySelectItemName,index:this.curIndex});
                
                let name = this.mySelectItemName;
                this.$emit('isActive',name);
            }
             
            this.isSelectBlock = false;
        },
        ...mapMutations(['isSelectedPush'])
    
    }

}
</script>

<style>

</style>



