<template>
    <div class="right-side-goodsList-box">
        <h2>猜你喜欢</h2>
        <EveryGoodsItem v-for="(item,index) in rightSideList" :key="index" :goodsListObj="item"
           boxNewClass="true" scoreDisPlay="block" contentNewClass="true"/>
    </div>
</template>

<script>
import EveryGoodsItem from '@/components/RouterView/goodsList/EveryGoodsItem.vue'
import {mapState} from 'vuex'
export default {
   components:{
       EveryGoodsItem
   },
   computed:{
       ...mapState(['rightSideList'])
   }
}
</script>

<style lang="less">
  @import url('../../../assets/goodsList/rightsidegoodslist.less');
</style>



